import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {ITransferServiceProvider} from "../../providers/i-transfer-service/i-transfer-service";

@IonicPage()
@Component({
  selector: 'page-add-credit-account02',
  templateUrl: 'add-credit-account02.html',
})
export class AddCreditAccount02Page {

  addCreditAccount02Error = "";
  accDetails ;
  accountNo;
  title;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public iTransferService: ITransferServiceProvider,
              public alertCtrl : AlertController) {

    this.accDetails = JSON.parse(this.navParams.get("accDetails"));
    this.accountNo=this.accDetails.subscriptionValue;
    this.title=this.accDetails.subscriptionDesc;
  }

  AddNewCreditAccount02Action = function () {

    this.addCreditAccount02Error = "";
    let flag = true;

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let oneTimeKey="";
      let appData = this.storage.retrieveFromStorage("appData");
      let encAccountNo = this.cryptoUtil.getEncryptVal(this.accountNo);
      let encOtp = this.cryptoUtil.getEncryptVal(oneTimeKey);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.iTransferService.addCreditAccountConfirm(unMaskToken, appData.userId, appData.appId, encAccountNo, encOtp)
        .then(data => {
          this.onConfirmAddCreditAccComplete(data);
        });
    }
  };

  onConfirmAddCreditAccComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('AddCreditAccount03Page', {});
    }

    else if (data.statusCode == 0) {
      this.addCreditAccount02Error = data.note;
    }

    else {
      this.addCreditAccount02Error = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {
    // console.log('ionViewDidLoad AddCreditAccount02Page');
  }

}



